#about {
  body {
    margin: 10px;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  h1 {
    padding: 10px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    text-align: center;
  }
  h2 {
    padding: 10px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    text-align: center;
    font-size: 15px;
  }
  h3 {
    padding: 10px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    font-size: 15px;
    line-height: 0em;
  }
  #name {
    text-align: center;
  }

  ul {
    text-align: left;
  }
  li {
    margin-bottom: 5px;
    text-align: justify;
    margin-right: 10px;
    margin-left: 10px;
  }

  p {
    padding: 10px;
    text-align: justify;
  }
  a {
    padding: 10px;
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }

  .bold {
    font-weight: bold;
  }
}
